h6 {
  padding-top: none;
  text-align: center;
  padding-bottom: 1rem;
}

.MenuInfo {
 /** font-family: "Lora1" !important;**/
}

ion-menu ion-content {
  --background: var(--ion-item-background, var(--ion-background-color, #fff));
}

ion-menu.md ion-item {
  --padding-start: 10px;
  --padding-end: 10px;
  border-radius: 4px;
}

ion-menu.md ion-item.selected {
  --background: rgba(var(--ion-color-primary-rgb), 0.14);
}

ion-menu.md ion-item.selected ion-icon {
  color: var(--ion-color-primary);
}

ion-menu.md ion-item ion-icon {
  color: #616e7e;
}

ion-menu.md ion-item ion-label {
  font-weight: 500;
}

ion-menu.icon ion-content {
  --padding-bottom: 20px;
}

ion-menu.icon ion-item {
  --padding-start: 16px;
  --padding-end: 16px;
  --min-height: 50px;
}

ion-menu.icon ion-item ion-icon {
  font-size: 24px;
  color: #73849a;
}

ion-menu.icon ion-item .selected ion-icon {
  color: var(--ion-color-primary);
}

ion-menu.icon ion-list#labels-list ion-list-header {
  margin-bottom: 8px;
}

ion-item.selected {
  --color: var(--ion-color-primary);
}
